*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}
body{
  background-color: black;
  overflow-x: hidden;
  position: relative;

}
html {
  scroll-behavior: smooth;
  scroll-padding-top: 75px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  line-height: normal;
}

p,
a,
li,
button,
ul {
  margin: 0;
  padding: 0;
  line-height: normal;
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}
/*Inicio*/
.banner{
  background-image: url(./Archivos/Imagenes/bannerfondo.jpg);
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  
}
.bannerrow{
 
  display: flex;
align-items: center;
justify-content: center;
}
.bannertext{
  display: inline-block;
  margin-left: 200px;
  
  
}
h1{
  font-family: 'Merienda One', cursive;
  color: #eaff82;
  font-size: 120px;
  text-align: center;
}
.apellido{
  margin-top: -50px;
}
ul li a{
  text-align: center;
  padding: 10px 60px;
  
  color: black;
  
  font-family: 'Montserrat', sans-serif;

}
ul li a:hover{color: #eaff82;}

ul li:hover{
  background-color: black;
 
}
ul li{
  border-radius: 35px;
  width: 120%;
  height: 50px;
  background-color: #eaff82;
  margin: 50px 0;
  text-align: center;
  align-items: center;
  justify-content: center;
  display: flex;
  
}





